.chapter-item{display:flex;flex-direction:row;padding:40px 0;counter-reset:section;counter-increment:chapter}.chapter-item:nth-child(2n){flex-direction:row-reverse}.chapter-item:nth-child(2n) .chapter-item__left{text-align:center}.chapter-item__left{flex-grow:1}.chapter-item__left-content{display:inline-block;text-align:left}.chapter-item__right{width:640px;height:420px;overflow:hidden}.chapter-item__title{font-size:24px;line-height:70px;color:#333;border-bottom:1px solid #D6D6D6;position:relative}.chapter-item__title::after{content:"";position:absolute;bottom:0;left:0;width:24px;height:4px;background-color:#666}.chapter-item__sections{margin-top:24px}.chapter-item__section{color:#666;font-size:16px;counter-increment:section}.chapter-item__section::before{content:counter(chapter) "." counter(section) "   "}.chapter-item__section+.chapter-item__section{margin-top:20px}.chapter-item__cover{display:block;width:100%;height:100%;object-fit:cover;background-color:#eee;transition:transform 3s}.chapter-item__cover:hover{transform:scale(1.1)}
