.block{
  padding:60px 0
}

.block__head{
  position:relative;
  text-align:center;
  font-size:32px;
  line-height:1.4;
  height:calc(72px * 1.4);
  box-sizing:border-box;
  padding-top:calc((72px - 32px) * 1.2 - 7px);
  font-weight:500;
  margin-bottom:40px
}

.block__head::before{
  content:attr(data-content);
  text-transform:uppercase;
  position:absolute;
  font-size:72px;
  font-weight:bold;
  color:rgba(51,51,51,0.05);
  top:0;
  left:50%;
  -webkit-transform:translate3d(-50%, 0, 0);
          transform:translate3d(-50%, 0, 0)
}

.block__head::after{
  content:"";
  display:block;
  position:absolute;
  width:64px;
  height:8px;
  background-color:#f47e14;
  bottom:0;
  left:50%;
  -webkit-transform:translate3d(-50%, 0, 0);
          transform:translate3d(-50%, 0, 0)
}

